<script>
import ChatbotPlanForm from '@/components/ChatbotPlanForm.vue';


export default {
  name: 'ChatbotView',
  components: {
    ChatbotPlanForm
  },
  data() {
    return {
      basicSelected: false,
      connectSelected: false,
      advancedSelected: false,
    }
  },
  methods: {
    toggleSelectedPlan(selected) {
      switch (selected) {
        case 'basicSelected':
          this.basicSelected = true;
          this.connectSelected = false;
          this.advancedSelected = false;
          break;
        case 'connectSelected':
          this.connectSelected = true;
          this.basicSelected = false;
          this.advancedSelected = false;
          break;
        case 'advancedSelected':
          this.advancedSelected = true;
          this.basicSelected = false;
          this.connectSelected = false;
      }
    },
  }
}
</script>

