<template>
  <div>
    <p style="margin-bottom: 0;">{{ this.infoMessage }}</p><br>
    <label style="font-size: 0.9em;">Por favor, insira seu nome:</label>
    <input style="width: 100%;padding: 0; border-left: 0;border-right: 0;border-top: 0;" type="text" v-model="name"
      placeholder="Digite aqui..." autocomplete="name"><br><br>
    <label style="font-size: 0.9em;">Insira um número do WhatsApp para entrarmos em contato:</label>
    <input style="width: 100%;padding: 0; border-left: 0;border-right: 0;border-top: 0;" type="text" v-model="number"
      placeholder="Digite aqui..." autocomplete="tel-local"><br>
    <button @click="submitForm">Enviar</button>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'ChatbotPlanForm',
  props: {
    plan: {
      type: String
    },
  },
  data() {
    return {
      name: null,
      number: null,
      infoMessage: null
    }
  },
  methods: {
    submitForm() {

      if(!this.name || !this.number){
        return this.infoMessage = 'Por favor, preencha todos os campos'
      }

      if(this.number.length < 10){
        return this.infoMessage = 'Por favor, insira o DDD'
      }
      const messageApi = `
Solicitação de plano através do Site

Plano: ${this.plan}
Nome: ${this.name}
Contato: ${this.number}
`;

    this.sendMessageWithApi(messageApi)

    },
    sendMessageWithApi(message) {
      axios.post('https://soluciona.inf.br/api/sendEvol.php', {
        message: message
      }, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
        .then(() => {
          this.infoMessage = 'Mensagem enviada com sucesso, logo entraremos em contato!'
        })
        .catch(error => {
          console.error('Erro na requisição:', error);
        });

    }
  }
}
</script>

