div {
  animation: open .2s ease-in;
  overflow: hidden
}

label {
  font-weight: bold
}

button {
  background-color: #E7CD56;
  border: none;
  margin-top: 5px;
  padding: 4px 0;
  width: 100%;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

@keyframes open {
  0% {
    height: 0;
  } 
  100% {
    height: 170px;
  }
  
}
</style>
